			metadata: 'confirmed-y',
			creator: 'John Pearson, editor. ',
			textTitle: 'Critici sacri, sive, Doctissimorum virorum in SS. Biblia annotationes, & tractatus.',
			place: 'London, England. ',
			year: '1660. ',
			narrative: 'With all of their scholastic apparatus, it can be easy to forget that polyglots were still Bibles. As such, the creators of the great polyglots expected people to use them to learn about God. Indeed, polyglots were considered the most definitive editions of biblical texts available that would reveal the spiritual meaning of the Word in its deepest sense. Since using them was no easy task, however, scholars also developed materials to help readers get as much spiritual value out of the polyglots as possible. The text shown here, a collection of commentaries on all the books of the Bible, is an example of one of these aids. This project was spearheaded by John Pearson, one of the early advocates for the London polyglot, and was perhaps intended to supplement Walton’s great work.', //comment on extending this info in word document.
			link: 'https://i-share.carli.illinois.edu/nby/cgi-bin/Pwebrecon.cgi?DB=local&v1=1&BBRecID=818407',
