			metadata: 'confirmed-y',
			creator: 'Brian Walton. ',
			textTitle: 'Introductio ad Lectionem Linguarum Orientalium. ',
			place: 'London, England. ',
			year: '1655. ',
			narrative: 'Polyglots were made to be used, but their creators did not assume that anyone would be able to do this without help. They worked hard to prepare the public to be able to get the most use out of them. For instance, one of Walton’s primary goals for his polyglot was to prepare missionaries to correct troubling religious practices and beliefs among Near Eastern Christians. This short work, published two years before the polyglot was finished, was intended to help achieve this goal. It taught readers how to read, write, and pronounce the Near Eastern languages that would appear in the polyglot. With this background, the London polyglot became a kind of textbook that missionaries and scholars could use to communicate with Eastern Christians in their native tongues.',
			link: 'https://i-share.carli.illinois.edu/nby/cgi-bin/Pwebrecon.cgi?DB=local&v1=1&BBRecID=764078',
